/* unplugin-vue-components disabled */import { defineComponent, ref, watch } from 'vue';
import { useStore } from 'vuex';
import LbForm from '@/base-ui/form';
export default defineComponent({
  props: {
    modalConfig: {
      type: Object,
      required: true
    },
    modalFormConfig: {
      type: Object,
      default: () => ({})
    },
    pageName: {
      type: String,
      required: true
    },
    otherInfo: {
      type: Object,
      default: () => ({})
    }
  },
  components: {
    LbForm
  },

  setup(props) {
    // dialogVisible
    const dialogVisible = ref(false);
    const formDate = ref({}); // watchmodalConfig
    // 

    let Ititle = ref(props.modalConfig.title);
    watch(() => props.modalFormConfig, newVal => {
      for (const item of props.modalConfig.formItems) {
        formDate.value[`${item.field}`] = newVal[`${item.field}`];
      } // 


      if (newVal.id) {
        // 2
        Ititle.value = `${Ititle.value.slice(-2)}`;
      } else {
        Ititle.value = `${Ititle.value.slice(-2)}`;
      }
    });

    const handleClose = done => {
      ElMessageBox.confirm('?').then(() => {
        done();
      }).catch(() => {// catch error
      });
    }; // 


    const store = useStore();

    const handleConfirmClick = () => {
      dialogVisible.value = false;

      if (Object.keys(props.modalFormConfig).length) {
        // 
        store.dispatch('system/updataPageDataAction', {
          pageName: props.pageName,
          editData: { ...formDate.value,
            ...props.otherInfo
          },
          id: props.modalFormConfig.id
        });
      } else {
        // 
        store.dispatch('system/createPageDataAction', {
          pageName: props.pageName,
          newData: { ...formDate.value,
            ...props.otherInfo
          }
        });
      }
    };

    return {
      formDate,
      dialogVisible,
      handleClose,
      handleConfirmClick,
      Ititle
    };
  }

});